.heading-correct-pronounciation {

  margin: auto;
  text-align: center;
  position: relative;
}


h3 {
  color:black;
  font-family: 'Cardo', serif;
  font-size: .5em;
  font-weight: normal;
  font-style: italic;
  letter-spacing: 0.1em;
  line-height: 2em;
}

em {
  font-family: 'EB Garamond', serif;
  font-size: 1.14em;
  text-transform: uppercase;
  letter-spacing: 0.1em;
  display: block;
  font-style:normal;
  padding-top: 0.1em;
  text-shadow: 0.07em 0.07em 0 rgba(0, 0, 0, 0.1);
}
em::before{
    content: "ยง";
    display: inline-block;
    -webkit-transform: rotate(90deg);
    transform: rotate(90deg);
    opacity: 0.2;
    margin: 0 0.6em;
    font-size: 0.5em;
}
em::after{
    content: "ยง";
    display: inline-block;
    -webkit-transform: rotate(90deg);
    transform: rotate(90deg);
    opacity: 0.2;
    margin: 0 0.6em;
    font-size: 0.5em;
}



